.modal {
  width: 100%;
  z-index: 1;
  position: fixed;
  display: flex;
  visibility: hidden;
  opacity: 0;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: visibility 0s, opacity 0.5s linear;
}
